.contactPage {
    background-color: #28367e;
    color:white;
    height: 100%;
    width: 100%;
}

.contactTitle{
    font-size: 5vw;
    text-decoration: none;
    text-align: center;
}



.contactContent, .singleLineContactInfo{
    --background: #28367e;
    --placeholder-color: #28367e;
    --color: #28367e;
    --placeholder-font-weight: 500;
} 

.contactForm{
    padding-left: 15%;
    padding-right: 15%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 3vw;
}

ion-toast { 
    --background: white;
    --color: #28367e;
    --start: right;
    --width: 25vw;
}


.contactNameEmail{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 50vh;
    width: 50%;
}

.singleLineContactInfo{
    height: 10vh;
}

.contactContent{
    height: 43vh;
    background-color: purple;
}

.contactContentSection{
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 50vh;

}

.contactSubmit{
    justify-self: flex-end;
}
