.section {
    padding-left: 4%;
    padding-right: 4%;
    background-color: #28367E;
    height: 50%;
    color: white;
    padding-bottom: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.buttons {
    display: flex;
    justify-content: space-evenly; 
    gap: 50%
}

.name {
    font-size: 7vw;
    color: white;
    text-decoration: underline;
    margin-bottom: 2%;
}

.caption {
    margin-top: 0;
    font-size: 3vw;
    text-wrap: pretty;
}


ion-button {
    --size: large;
    color: white;
    --border-color: white;
}
