    display: flex;
    flex-basis: 100%;
    justify-content: space-around; 
    align-items: center;
    gap: 20px;

    img{
        width: auto;
        height: 10rem;
    }
}

.written-text{
    color: #4554a4;
    font-weight: 500;
    font-size: 1.2rem;
}