.blogpostList {
    padding-left: 20%;
    padding-right: 20%;
    background-color: white;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.blogpostDisplayItem img {
    width: 30vw;
    height: 15vh;
    object-fit: none;
}

.blogpostDisplayItem{ 
    display: flex;
    color:#28367E;
    border-style: solid;
    border-color: #28367E;
    border-width: thin;
}

.blogpostTitleCard {
    font-size: 2.5rem !important;
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 0;
    text-wrap:pretty;
    align-self: flex-start;
}

.blogpostWordSection{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 15vh;
    padding-left: 2%;
    padding-right:3%;
    justify-content: space-around;
}

.blogpostCardCaption{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 1.5vh;
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 500;
}